











export default {
	data() {
		return {
			src:"",
			src_choose:""
		};
	},
	methods:{
		takePhoto(){
			 const ctx = uni.createCameraContext();
			 ctx.takePhoto({
			 	 quality: 'high',
				success:(res) => {
					console.log(res)
					this.src = res.tempImagePath
				}
			 })
		},
		choosePhoto(){
			uni.chooseImage({
				success:(e) => {
					console.log(e.tempFilePaths)
					this.src_choose = e.tempFilePaths[0]
				}
			})
		}
	}
}
